@import '../../../styles/settings.scss';

.root {
	width: 100px;
	height: 100px;
	background-color: transparent;
	margin-top: 2rem;
	z-index: 9999;
	.loadingSpinner {
		border: 7px solid $color-yellow;
		border-top: 7px solid $color-background-dark;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		animation: spin 1.5s linear infinite;
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
